import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);

let router = new Router({
  mode:"history",
  routes: [
    {
    	path: '/',
    	name: '首页',
    	component: (resolve) => {
    		require(['URL_page/Home/index.vue'], resolve);
    	}
    },
    {
    	path: '/order',
    	name: '订单',
    	component: (resolve) => {
    		require(['URL_page/Order/order.vue'], resolve);
    	}
    },
    {
    	path: '/orderDetail',
    	name: '订单详情',
    	component: (resolve) => {
    		require(['URL_page/Order/OrderDetail.vue'], resolve);
    	}
    },
  ]
});

export default router ;


