require('./assets/css/common.css');
import Vue from 'vue';
import App from './App';
import router from './router';
import store from '@/store/index';
//引入we-vue 框架
import WeVue from 'we-vue';
import 'we-vue/lib/style.css';
Vue.use(WeVue);

//引入iconfonts(文字图标)
import './assets/iconfonts/iconfont.css';

//axios请求方式
import axios from 'axios';
Vue.prototype.$axios=axios;

Vue.config.productionTip = false;

//过滤器，所有请求前调用，next返回response处理
axios.interceptors.request.use(
  (config) => {
	if (localStorage.qhwsc_token) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
    config.headers.Authorization = localStorage.qhwsc_token;
  }else{
    //这里如果检测到没有token的话就要跳授权页面重新获取token
  }
	return config;
  },err => {
    return Promise.reject(err);
  }
);



// http response 响应拦截器
import { Toast } from 'we-vue';     //纯文本提示组件
axios.interceptors.response.use(data => {
	if(data.data.status == 0){
		Toast({
  		duration: 2000,
  		message: data.data.msg ? data.data.msg : '服务器异常，请稍后重试！',
  		type: 'text'
		})
	}
	return data;
}, error => {
	return Promise.reject(error)
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



