import Vue from 'vue';
import Vuex from 'vuex';
import * as actions from 'URL_store/actions';
import * as getters from 'URL_store/getters';
import * as mutations from 'URL_store/mutations';

/*模块*/
import ORDER_LIST from 'URL_store/modules/order/order_list';
import ORDER_DETAIL from 'URL_store/modules/order/order_detail';
import PRODUCT_LIST from 'URL_store/modules/product/product_list';

Vue.use(Vuex);

export default new Vuex.Store({
  getters,
  mutations,
  actions,
  modules:{
  	ORDER_LIST,
  	ORDER_DETAIL,
  	PRODUCT_LIST
  }
})


